body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*:after,
*:before {
  margin: 0;
}
.main {
    padding-left: 30px;
    padding-right: 30px;
    background-color: #eaeaea;
    min-height: calc(100vh - 60px);
}
.big-screen {
    height: 60px;
    background-color: #07393C;
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media (max-width: 569.09px) {
    .big-screen {
        display: none;
    }
}

@media (max-width: 569.6px) {
    .big-screen {
        display: none;
    }
}

.big-screen ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    line-height: 60px;
}

.small-screen {
    height: 60px;
    background-color: #07393C;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

@media (min-width: 570px) {
    .small-screen {
        display: none;
    }
}

.small-screen .nav-sidedrawer .toggleButton {
    cursor: pointer;
    width: 42px;
    height: 42px;
}

.small-screen .nav-sidedrawer .toggleButton span:first-child {
    position: absolute;
    display: inline-block;
    width: 40px;
    height: 3px;
    background-color: white;
    top: 15px;
}

.small-screen .toggleButton span:nth-child(2) {
    position: absolute;
    top: 25px;
    display: inline-block;
    width: 40px;
    height: 3px;
    background-color: white;
}

.small-screen .toggleButton span:nth-child(3) {
    position: absolute;
    display: inline-block;
    width: 40px;
    height: 3px;
    background-color: white;
    top: 35px;
}

.small-screen .nav-sidedrawer .sidedrawer {
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    background-color: #dedede;
    width: 200px;
    height: 100%;
    box-shadow: 2px 2px 7px -1px black;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.small-screen .nav-sidedrawer .sidedrawer .sidedrawer-close {
    text-align: right;
    cursor: pointer;
}

.small-screen .nav-sidedrawer .sidedrawer .sidedrawer-close p {
    margin-right: 10px;
    font-weight: bolder;
}

.small-screen .nav-sidedrawer .sidedrawer .sidedrawer-nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
}
.nav-li {
    margin-left: 7px;
    margin-right: 7px;
    font-size: 14px;
}

@media (max-width: 569.09px) {
    .nav-li {
        margin: 0;
    }
    .nav-li:not(:first-child) {
        margin-top: 5px;
    }
}

.nav-li a {
    text-decoration: none;
    color: white;
}

.nav-li a:hover,
.nav-li a:active,
.nav-li a.active {
    color: #FFA400;
}

@media (max-width: 569.09px) {
    .nav-li a {
        display: inline-block;
        padding: 10px;
        width: 90%;
        font-weight: bolder;
        color: blue;
    }
    .nav-li:hover {
        background-color: #07393C;
    }
    .nav-li a:hover,
    .nav-li a:active {
        color: #FFA400;
    }
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .9);
    z-index: 98;
}

.error_message_parent {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: white;
    padding: 8px 15px;
    box-shadow: 3px 4px 4px -2px grey;
}

.error_message {
    font-size: 20px;
}
.mobiles-title {
    text-align: center;
    padding-top: 20px;
    text-transform: capitalize;
}

.mobiles-title span {
    color: #07393C;
}

.mobiles {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-top: 20px;
}

.Nomobiles {
    text-align: center;
    margin-top: 70px;
    margin-left: auto;
    margin-right: auto;
    font-size: 50px;
    font-weight: 900;
}

.item-added-parent {
    width: 250px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: white;
    padding: 10px;
    text-align: center;
}

.item-added-parent p span {
    font-size: 20px;
    font-weight: bold;
}

.item-added-parent .item-added-image {
    margin-top: 20px;
}

.item-added-parent .item-added-image img {
    width: 100px;
    height: 150px;
}

.buttonn {
    padding: 9px 12px;
    border-radius: 5px;
    background-color: #07393C;
    border: none;
    cursor: pointer;
    color: #FFA400;
    font-weight: 600;
    outline: none;
}

button:disabled {
    background-color: rgba(7, 57, 60, .6);
    color: rgba(255, 164, 0, .6);
    cursor: auto;
}
.mobile-card {
    width: 200px;
    min-height: 275px;
    border-radius: 7px;
    flex: 0 0 23%;
    background-color: white;
    margin-left: 5px;
    margin-right: 5px;
    position: relative;
    overflow: hidden;
    padding-bottom: 6px;
}

@media (min-width: 820px){
    .mobile-card {
        flex: 0 0 23%;
    }
}

@media (min-width: 650px) and (max-width: 819px){
    .mobile-card {
        flex: 0 0 30%;
    }
}

@media (min-width: 450px) and (max-width: 649px){
    .mobile-card {
        flex: 0 0 45%;
    }
}

@media (min-width: 350px) and (max-width: 449px){
    .mobile-card {
        flex: 0 0 70%;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }
}

@media (max-width: 349px){
    .mobile-card {
        flex: 0 0 95%;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }
}



.mobile-card:not(:first-of-type),
.mobile-card:not(:nth-of-type(2)),
.mobile-card:not(:nth-of-type(3)),
.mobile-card:not(:nth-of-type(4)) {
    margin-top: 10px;
}

.mobile-card .mobile-image {
    width: 100%;
    height: 180px;
}

.mobile-card .mobile-image img {
    width: 100%;
    height: 100%;
}

.mobile-card .mobile-type-price {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    padding-left: 7px;
    padding-right: 7px;
}

.mobile-card .mobile-type-price .mobile-type p {
    font-size: 16px;
}

.mobile-card .mobile-type-price .mobile-price p {
    font-size: 20px;
    font-weight: bold;
}

.mobile-card .buttons {
    text-align: center;
    margin-top: 10px;
}

.mobile-card .buttons button {
    margin-left: 3px;
    margin-right: 3px;
}

.mobile-card .AddToCart {
    position: absolute;
    right: 0px;
    bottom: 172px;
    padding: 5px 6px;
    border-radius: 5px;
    -webkit-transform: translateX(101%);
            transform: translateX(101%);
    -webkit-transition: all 1s;
    transition: all 1s;
}

.mobile-card:hover .AddToCart {
    -webkit-transform: translateX(6px);
            transform: translateX(6px);
}

.mobile-card .createdby {
    padding-left: 7px;
    padding-right: 7px;
}

.image-selected img {
    width: 200px;
    height: 200px;
}
.Loader {
  color: #07393C;
  font-size: 20px;
  margin: 100px auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load4 1.3s infinite linear;
  animation: load4 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  text-align: center;
  margin-top: 100px;
}
@-webkit-keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}
@keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}

